* {
    box-sizing: border-box;
    font-family: sans-serif;
}
.navbar {
    background: rgb(1, 70, 70);
    position: fixed;
    width: 100%;
    min-height: 70px;
    color: white;
    display: block;
    font-size: 30px;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}
.navbar li {
    display: inline-block;
    padding-top: 15px;
    width: 33.3%;
    text-align: center;
}

.header {
  background-color: rgb(180, 142, 51);
  background-image: url(/Clicky-Game/static/media/main_bg.de77348f.png);
  background-repeat: repeat;
  color: #fff;
  min-height: 400px;
  padding: 180px 20px;
  padding-bottom: 140px;
  text-align: center;
  display: block;
}

.images {
  display: inline-block;
  background-size: cover;
  background-position: 50%;
  height: 170px;
  width: 170px;
  margin: 10px 30px;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
  cursor: pointer;
}

.images:hover {
  -webkit-transform: translate(-3px, -3px);
  transform: translate(-3px, -3px);

}
.footer {
  background: rgb(1, 112, 112);
  height: 100px;
  position: relative;
  color: #fff;
  overflow: auto;
  
}
.footer>.bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgb(1, 70, 70);
  padding: 8px;
  font-weight: 600;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.container {
  display: flex;
  min-height: 80%;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 90%;
  margin: 0 auto;
}
